.banner {
	width: 100%;
	background-position: center center;
	background-size: cover;
	background-attachment: fixed;
}

.header {
	position: fixed;
	top: 0;
	width: 100%;
	padding: 20px;
	height: 30px;
	z-index: 1;

	display: flex;
	justify-content: space-between;

	/* animation */
	transition: all 0.3s;
	transition-timing-function: ease-in;
}

.header__black {
	background-color: #111;
}

.header__logo {
	position: fixed;
	left: 20px;
	width: 80px;
	object-fit: contain;
	cursor: pointer;
}

.header__avatar {
	position: fixed;
	right: 20px;
	width: 30px;
	object-fit: contain;
	cursor: pointer;
}
